.xinyuandan {
    width: 90%;
    margin: 0 auto;
    .am-tabs-default-bar-top {
        .am-tabs-default-bar-underline {
            border-color: #008489;
        }
    }
    .nocollect {
        font-size: 0.32rem;
        text-align: center;
        line-height: 1rem;
    }
    .fill {
        margin-bottom: 0.9rem;
        .clbox {
            width: 100%;
            padding: 0 0.1rem;
            li {
                width: 100%;
                padding: 0.3rem 0;
                box-sizing: border-box;
                border-bottom: 1px solid #eee;
                .climg {
                    width: 100%;
                    img {
                        width: 100%;
                        height: 4rem;
                        border-radius: 5px;
                    }
                }
                .cltxt {
                    padding: 0.2rem 0;
                    h2 {
                        font-size: 0.3rem;
                        font-weight: bold;
                        line-height: 0.6rem;
                    }
                    p {
                        font-size: 0.28rem;
                        margin-top: 0.2rem;
                        span {
                            color: #008489;
                            margin-right: 0.2rem;
                        }
                    }
                }
            }
        }
    }
}